import React, {SyntheticEvent, useState} from 'react';
import './App.css';
import AddText from "./components/AddText";
import ListStamps from "./components/ListStamps";
import createStamp, {LifeStamp, merge} from "./lib/stamps.utils";
import useLocalList from "./components/hooks/localStorage.hook";
import InputFile from "./components/InputFile";
import downloadFile from "./icons/file_download_white_24dp.svg";

function App() {
    const {addAtBeginning: addStamp, fetch: fetchStamps, clear: clearStamps} = useLocalList('LIFESTAMPS');
    const [newText, setNewText] = useState('');
    const [stamps, setStamps] = useState(fetchStamps<LifeStamp>());

    function onInput(e: SyntheticEvent<HTMLInputElement>) {
        setNewText(e.currentTarget.value);
    }

    function onSubmit(e: SyntheticEvent<HTMLButtonElement>) {
        if (newText) {
            const stamp = createStamp(newText, stamps.length + 1);
            setStamps([stamp, ...stamps]);
            addStamp(stamp);
            setNewText('');
        }
    }

    function onFileRead(data: LifeStamp[]) {
        const mergedLifeStamps = merge(stamps, data);
        clearStamps();
        mergedLifeStamps.forEach((stamp: string) => addStamp(stamp));
        setStamps(mergedLifeStamps);
    }

    return (
        <div className="App">
            <header className="header">
                <h2 className="brand"><a href="/lifestamps">LifeStamps</a></h2>
            </header>
            <section className="content">
                <AddText value={newText} btnActions={{onSubmit}} inputActions={{onInput}}/>
                <ListStamps list={stamps}/>
            </section>
            <footer className="footer">
                <div className="controls">
                    <div className="control">
                        <span>json: </span>
                        <span>
                            {stamps.length > 0 &&

                            <a href={`data:text/json;charset=utf-8,${encodeURIComponent(JSON.stringify(stamps, null, 4))}`}
                               download={`lifestamps-${new Date().toLocaleDateString()}.json`}>
                                <img src={downloadFile} alt="download your lifestamps as json"/>
                            </a>
                            }
                        </span>
                        <span>
                            <InputFile actions={{onFileRead}}/>
                        </span>
                    </div>
                </div>
                <h6>lifestamps by anubhav | 0.11.0-55</h6>
            </footer>
        </div>
    );
}

export default App;
