import React from "react";
import {LifeStamp} from "../lib/stamps.utils";

export default function ListStamps(props: any) {
    const {list} = props;
    return (
        <div className="stamps">
            {list && list.length > 0 && list.map(({note, ts}: LifeStamp, i: number) => {
                return (<div className="stamp" key={`list-item-${i}`}>
                    <div className="ts">
                        {ts}
                    </div>
                    <div className="note"
                         dangerouslySetInnerHTML={{__html: note.replace(/(\#\w+)/g, '<span class="highlighted">\$1</span>')}}>
                        {/*{note.replace(/(?<hash>#\w+)/g, '<span>\$hash</span>')}*/}
                        {/*{note.replace(/(\#\w+)/g, '<span>\$1</span>')}*/}
                    </div>
                </div>);
            })}
        </div>
    );
}
